// Ce fichier est le plus important de l'application. Il permet de crer le store Redux. Il permet de rendre l'application dans la div root. Il permet de charger les tours au dmarrage de l'application. Il permet de combiner les reducers. Il permet de dfinir les dpendances et composants REDUX. Il permet de dfinir les dpendances et composants React. Il permet de dfinir les dpendances et composants SCSS.

import React from "react";
import ReactDOM from "react-dom/client";
import App from "./App";
import "react-responsive-carousel/lib/styles/carousel.min.css";

// import Fontawesome


import "./styles/index.scss";

// Dpendances et composants REDUX
import { configureStore } from "@reduxjs/toolkit";
import { Provider } from "react-redux";
import rootReducer from "./reducers";
import { getAllTours } from "./actions/tour.action";
import { setMenu } from "./actions/menu.action";

// On cre le store Redux
const store = configureStore({
  reducer: rootReducer,
  devTools: true,
});
store.dispatch(getAllTours());
store.dispatch(setMenu(false));

// On utilise la nouvelle API React 18
const root = ReactDOM.createRoot(document.getElementById("root"));

// On rend l'application dans la div root
root.render(
  <Provider store={store}>
    <App />
  </Provider>
);
